
import { NgModule, ApplicationRef, DoBootstrap } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CardFooterComponent } from './card-footer.component';
import { CardFooterDefinition } from '@fabric-msft/fabric-web';

@NgModule({
  declarations: [CardFooterComponent],
  imports: [CommonModule],
  exports: [CardFooterComponent]
})
export class CardFooterProxyModule implements DoBootstrap {
  constructor() {
    CardFooterDefinition.define();
  }
  ngDoBootstrap(appRef: ApplicationRef) {}
}
