import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
import { TeachingBubblePlacement, TeachingBubbleSize } from '@fabric-msft/fabric-web';
@Component({
  selector: 'fabric-teaching-bubble',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeachingBubbleComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Input() target: string = "";
  @Input() placement: TeachingBubblePlacement = "top";
  @Input() open: boolean;
  @Input() disableTrapFocus: boolean = false;
  @Input() size: TeachingBubbleSize = "medium";
  @Output() openchange = new EventEmitter<any>();
  @Output() dismiss = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.target) {
          this.renderer.setAttribute(nativeElement, 'target', this.target);
        }
    if (this.placement) {
          this.renderer.setAttribute(nativeElement, 'placement', this.placement);
        }
    if (this.open) {
            this.renderer.setAttribute(nativeElement, 'open', '');
          }
    if (this.disableTrapFocus) {
            this.renderer.setAttribute(nativeElement, 'disable-trap-focus', '');
          }
    if (this.size) {
          this.renderer.setAttribute(nativeElement, 'size', this.size);
        }
  }
  private handleOpenchange = (event: any) => {
    this.zone.run(() => {
      this.openchange.emit(event.detail);
    });
  };
  private handleDismiss = (event: any) => {
    this.zone.run(() => {
      this.dismiss.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('openchange', this.handleOpenchange);
    this.el.nativeElement.addEventListener('dismiss', this.handleDismiss);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('openchange', this.handleOpenchange);
    this.el.nativeElement.removeEventListener('dismiss', this.handleDismiss);
  }
}
