import {
  Component,
  ElementRef,
  Input,
  NgZone,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges
} from '@angular/core';
import { MessageBarShape, MessageBarLayout, MessageBarIntent } from '@fabric-msft/fabric-web';
@Component({
  selector: 'fabric-message-bar',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MessageBarComponent implements OnInit, OnChanges {
  public zone: NgZone;
  @Input() shape: MessageBarShape | undefined;
  @Input() layout: MessageBarLayout | undefined;
  @Input() intent: MessageBarIntent | undefined;
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.shape) {
          this.renderer.setAttribute(nativeElement, 'shape', this.shape);
        }
    if (this.layout) {
          this.renderer.setAttribute(nativeElement, 'layout', this.layout);
        }
    if (this.intent) {
          this.renderer.setAttribute(nativeElement, 'intent', this.intent);
        }
  }
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
}
