import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
@Component({
  selector: 'fabric-multi-view',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiViewComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Input() hidden: boolean = true;
  @Output() hiddenchanged = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.hidden) {
            this.renderer.setAttribute(nativeElement, 'hidden', '');
          }
  }
  private handleHiddenchanged = (event: any) => {
    this.zone.run(() => {
      this.hiddenchanged.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('hiddenchanged', this.handleHiddenchanged);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('hiddenchanged', this.handleHiddenchanged);
  }
}
