import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
import { PositioningShorthandValues } from '@fabric-msft/fabric-web';
@Component({
  selector: 'fabric-tooltip',
  template: `
    <ng-content></ng-content>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TooltipComponent
  implements OnInit, OnChanges, AfterViewInit, OnDestroy
{
  public zone: NgZone;
  @Input() position: PositioningShorthandValues;
  @Input() open: boolean = false;
  @Input() beak: boolean = false;
  @Input() targetId: string;
  @Input() transitionDuration: number;
  @Input() transitionDelay: number;
  @Output() hidePopover = new EventEmitter<any>();
  @Output() showPopover = new EventEmitter<any>();
  constructor(
    private el: ElementRef,
    private renderer: Renderer2,
    private ngZone: NgZone
  ) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.position) {
      const positionValue = this.transformPosition(this.position);
      this.renderer.setAttribute(nativeElement, 'position', positionValue);
    }
    if (this.open) {
      this.renderer.setAttribute(nativeElement, 'open', '');
    }
    if (this.beak) {
      this.renderer.setAttribute(nativeElement, 'beak', '');
    }
    if (this.targetId) {
      this.renderer.setAttribute(nativeElement, 'target-id', this.targetId);
    }
    if (this.transitionDuration !== undefined) {
      this.renderer.setAttribute(
        nativeElement,
        'transition-duration',
        String(this.transitionDuration)
      );
    }
    if (this.transitionDelay !== undefined) {
      this.renderer.setAttribute(
        nativeElement,
        'transition-delay',
        String(this.transitionDelay)
      );
    }
  }

  private transformPosition(position: PositioningShorthandValues): string {
    // Transform the enum value to a valid DOM attribute value
    // Example transformation logic (to be adjusted as necessary)
    return position.toLowerCase().replace('_', '-');
  }

  private handleHidePopover = (event: any) => {
    this.zone.run(() => {
      this.hidePopover.emit(event.detail);
    });
  };
  private handleShowPopover = (event: any) => {
    this.zone.run(() => {
      this.showPopover.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener(
      'hidePopover',
      this.handleHidePopover
    );
    this.el.nativeElement.addEventListener(
      'showPopover',
      this.handleShowPopover
    );
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener(
      'hidePopover',
      this.handleHidePopover
    );
    this.el.nativeElement.removeEventListener(
      'showPopover',
      this.handleShowPopover
    );
  }
}
