import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
import { TagSize, TagShape, TagAppearance } from '@fabric-msft/fabric-web';
@Component({
  selector: 'fabric-tag',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TagComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Input() dismissButton: boolean = false;
  @Input() disabled: boolean = false;
  @Input() size: TagSize = "medium";
  @Input() shape: TagShape = "rounded";
  @Input() appearance: TagAppearance = "filled";
  @Output() dismiss = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.dismissButton) {
            this.renderer.setAttribute(nativeElement, 'dismiss-button', '');
          }
    if (this.disabled) {
            this.renderer.setAttribute(nativeElement, 'disabled', '');
          }
    if (this.size) {
          this.renderer.setAttribute(nativeElement, 'size', String(this.size));
        }
    if (this.shape) {
          this.renderer.setAttribute(nativeElement, 'shape', String(this.shape));
        }
    if (this.appearance) {
          this.renderer.setAttribute(nativeElement, 'appearance', String(this.appearance));
        }
  }
  private handleDismiss = (event: any) => {
    this.zone.run(() => {
      this.dismiss.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('dismiss', this.handleDismiss);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('dismiss', this.handleDismiss);
  }
}
