import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
@Component({
  selector: 'fabric-multi-view-group',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiViewGroupComponent implements AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Output() change = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private handleChange = (event: any) => {
    this.zone.run(() => {
      this.change.emit(event.detail);
    });
  };
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('change', this.handleChange);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('change', this.handleChange);
  }
}
