import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
@Component({
  selector: 'fabric-svg-icon',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SvgIconComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Input() name: string;
  @Input() size: string;
  @Input() path: string;
  @Input() ariaHidden: string = "true";
  @Input() focusable: string = "false";
  @Input() role: string = "";
  @Input() ariaLabel: string;
  @Output() error = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.name) {
          this.renderer.setAttribute(nativeElement, 'name', this.name);
        }
    if (this.size) {
          this.renderer.setAttribute(nativeElement, 'size', this.size);
        }
    if (this.path) {
          this.renderer.setAttribute(nativeElement, 'path', this.path);
        }
    if (this.ariaHidden) {
          this.renderer.setAttribute(nativeElement, 'aria-hidden', this.ariaHidden);
        }
    if (this.focusable) {
          this.renderer.setAttribute(nativeElement, 'focusable', this.focusable);
        }
    if (this.role) {
          this.renderer.setAttribute(nativeElement, 'role', this.role);
        }
    if (this.ariaLabel) {
          this.renderer.setAttribute(nativeElement, 'aria-label', this.ariaLabel);
        }
  }
  private handleError = (event: any) => {
    this.zone.run(() => {
      this.error.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('error', this.handleError);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('error', this.handleError);
  }
}
