import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
@Component({
  selector: 'fabric-table-cell',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TableCellComponent implements AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Output() hiddenchange = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private handleHiddenchange = (event: any) => {
    this.zone.run(() => {
      this.hiddenchange.emit(event.detail);
    });
  };
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('hiddenchange', this.handleHiddenchange);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('hiddenchange', this.handleHiddenchange);
  }
}
