import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges
} from '@angular/core';
@Component({
  selector: 'fabric-card-preview',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CardPreviewComponent implements OnInit, OnChanges {
  public zone: NgZone;
  @Input() fullWidth: boolean | undefined;
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.fullWidth) {
          this.renderer.setAttribute(nativeElement, 'full-width', String(this.fullWidth));
        }
  }
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
}
