import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges
} from '@angular/core';
@Component({
  selector: 'fabric-carousel',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CarouselComponent implements OnInit, OnChanges {
  public zone: NgZone;
  @Input() disableAnimation: boolean;
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.disableAnimation) {
            this.renderer.setAttribute(nativeElement, 'disable-animation', '');
          }
  }
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
}
