import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ChangeDetectionStrategy,
  Renderer2,
  OnInit,
  OnChanges,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
import { WizardStepState } from '@fabric-msft/fabric-web';
@Component({
  selector: 'fabric-wizard-step',
  template: `<ng-content></ng-content>`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WizardStepComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  public zone: NgZone;
  @Input() ordered: boolean;
  @Input() hideConnector: boolean;
  @Input() active: boolean = false;
  @Input() disabled: boolean = false;
  @Input() ariaDescribedby: string | undefined;
  @Input() ariaLabelledby: string | undefined;
  @Input() state: WizardStepState = "incomplete";
  @Output() stepchange = new EventEmitter<any>();
  constructor(private el: ElementRef, private renderer: Renderer2, private ngZone: NgZone) {
    this.zone = ngZone;
  }
  private updateComponentProperties() {
    const nativeElement = this.el.nativeElement;
    if (this.ordered) {
            this.renderer.setAttribute(nativeElement, 'ordered', '');
          }
    if (this.hideConnector) {
            this.renderer.setAttribute(nativeElement, 'hide-connector', '');
          }
    if (this.active) {
            this.renderer.setAttribute(nativeElement, 'active', '');
          }
    if (this.disabled) {
            this.renderer.setAttribute(nativeElement, 'disabled', '');
          }
    if (this.ariaDescribedby) {
          this.renderer.setAttribute(nativeElement, 'aria-describedby', this.ariaDescribedby);
        }
    if (this.ariaLabelledby) {
          this.renderer.setAttribute(nativeElement, 'aria-labelledby', this.ariaLabelledby);
        }
    if (this.state) {
          this.renderer.setAttribute(nativeElement, 'state', this.state);
        }
  }
  private handleStepchange = (event: any) => {
    this.zone.run(() => {
      this.stepchange.emit(event.detail);
    });
  };
  ngOnInit() {
    this.updateComponentProperties();
  }
  ngOnChanges() {
    this.updateComponentProperties();
  }
  ngAfterViewInit() {
    this.el.nativeElement.addEventListener('stepchange', this.handleStepchange);
  }
  ngOnDestroy() {
    this.el.nativeElement.removeEventListener('stepchange', this.handleStepchange);
  }
}
