
import { NgModule, ApplicationRef, DoBootstrap } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MultiViewComponent } from './multi-view.component';
import { MultiViewDefinition } from '@fabric-msft/fabric-web';

@NgModule({
  declarations: [MultiViewComponent],
  imports: [CommonModule],
  exports: [MultiViewComponent]
})
export class MultiViewProxyModule implements DoBootstrap {
  constructor() {
    MultiViewDefinition.define();
  }
  ngDoBootstrap(appRef: ApplicationRef) {}
}
